<template>
  <div id="app">
    <Game msg="Player vs Computer: Rock Paper Scissors"
    rules="The rules are simple:"
    paperwins="Papers Beats Rock"
    rockwins="Rock Beats Scissors"
    scissorwins="Scissors Beats Paper"/>

    <Options :opponentschoice="opponentschoice"/>
    <Results win="Wins"
    loss="Losses"
    draw="Draws"
    totalgames="Total Games"
    :wins="wins"
    :losses="losses"
    :draws="draws"
    :totgames="total"/>



  </div>
</template>

<script>
import Game from './components/Game.vue'
import Options from "@/components/Options";
import Results from "@/components/Results";

export default {
  name: 'App',
  data(){
  return{
    wins:0,
    losses:0,
    draws:0,
    total:0,
    opponentschoice:""
    }
  },components: {
    Options,
    Game,
    Results
  },methods: {
    rps:function(item) {
      const choices=["Rock","Paper", "Scissors"];
      var playerResult=choices[item];
      var result=Math.floor(Math.random()*3);
      var computerResult=choices[result];

      if(playerResult=="Rock"){
        if(computerResult=="Scissors"){
          this.incrementwins();
        }else if(computerResult=="Paper") {
          this.incrementloss();
        }else{
          this.incrementdraw();
        }
      }else if(playerResult=="Paper"){
        if(computerResult=="Rock"){
          this.incrementwins();
        }else if(computerResult=="Scissors") {
          this.incrementloss();
        }else{
          this.incrementdraw();
        }
      }else if(playerResult=="Scissors") {
        if (computerResult == "Paper") {
          this.incrementwins();
        } else if (computerResult == "Rock") {
          this.incrementloss();
        } else {
          this.incrementdraw();
        }
      }
      this.incrementtotal();
    },incrementwins(){
      this.wins++;
    },incrementloss(){
      this.losses++;
    },incrementdraw(){
      this.draws++;
    },incrementtotal(){
      this.total=this.draws+this.wins+this.losses;
    }
  }
}
</script>

